import logo from './logo.svg';
import Floor from './floor.png';
import './App.css';
import Axios from 'axios'
import React, { useState } from 'react'

function Stats() {

  const [FloorPrice, setStats] = useState("")
  const [TotalSupply, setCount] = useState("")
  const [Owners, setOwners] = useState("")
  const [DaySale, setDaySale] = useState("")
  const [WeekSale, setWeekSale] = useState("")
  const [MonthSale, setMonthSale] = useState("")
  const [TotalVolume, setTotalVolume] = useState("")

  const getStat = async () => {
    await Axios.get("https://api.opensea.io/api/v1/collection/teddybearz95/stats").then((response)=>
    {
      console.log(response.data.stats.floor_price);
      setStats(response.data.stats.floor_price.toFixed(2));
    })
    Axios.get("https://api.opensea.io/api/v1/collection/teddybearz95/stats").then((response)=>
    {
      setDaySale(response.data.stats.one_day_sales.toFixed(2));
    })
    Axios.get("https://api.opensea.io/api/v1/collection/teddybearz95/stats").then((response)=>
    {
      setWeekSale(response.data.stats.one_week_sales.toFixed(2));
    })
    Axios.get("https://api.opensea.io/api/v1/collection/teddybearz95/stats").then((response)=>
    {
      setMonthSale(response.data.stats.one_month_sales.toFixed(2));
    })
    Axios.get("https://api.opensea.io/api/v1/collection/teddybearz95/stats").then((response)=>
    {
      setTotalVolume(response.data.stats.total_volume.toFixed(2));
    })
    Axios.get("https://api.opensea.io/api/v1/collection/teddybearz95/stats").then((response)=>
    {
      console.log(response.data.stats.count);
      setOwners(response.data.stats.num_owners);
    })
  }

  return (
    <section className="stats">

      
      <div class="heading-text">
      <span class="letter"><h1>S</h1></span>
      <span class="letter"><h1>T</h1></span>
      <span class="letter"><h1>A</h1></span>
      <span class="letter"><h1>T</h1></span>
      <span class="letter"><h1>S</h1></span>




    </div>
        <table>
  <tr>
    <th>BEARZ FLOOR</th>
    <th>TOTAL VOLUME</th>
    <th>ACTIVE HOLDERS</th>
    <th>TOTAL SALES</th>
  </tr>
  <tr>
    <td><span>{FloorPrice}</span></td>
    <td><span>{TotalVolume}</span></td>
    <td><span>{Owners}</span></td>
    <td><span>{Owners}</span></td>
  </tr>

</table>
    <div class="things">
    <button onClick={getStat} class="btn">Get Stats</button>
    </div>

   
    </section>
  );
}

export default Stats;
